.card.theme__modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 200;
  text-align: center;
  box-shadow: 0 3rem 3rem rgba(0, 0, 0, 0.2);
  padding: 3rem;
  background: #dedede;
  opacity: 0;
  animation: animateModal 0.5s ease-in forwards;
}

@keyframes animateModal {
  to {
    opacity: 1;
  }
}

.card.theme__modal:hover {
  background: #dedede;
}

.card.theme__modal small {
  margin-top: 0.6rem;
  display: block;
  width: 80%;
  margin-inline: auto;
}

.theme__primary-wrapper {
  margin: 3rem 0;
}

.theme__primary-colors {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
  flex-wrap: wrap;
}

.theme__primary-colors button {
  width: 2rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  cursor: pointer;
}

.theme__primary-colors button.color-1 {
  background-color: hsl(270, 70%, 46%);
}
.theme__primary-colors button.color-2 {
  background-color: hsl(110, 70%, 46%);
}
.theme__primary-colors button.color-3 {
  background-color: hsl(240, 70%, 46%);
}
.theme__primary-colors button.color-4 {
  background-color: hsl(325, 70%, 46%);
}
.theme__primary-colors button.color-5 {
  background-color: hsl(0, 70%, 46%);
}
.theme__primary-colors button.color-6 {
  background-color: hsl(52, 70%, 46%);
}

.theme__background-colors {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1rem;
}

.theme__background-colors button {
  width: 7rem;
  height: 2.5rem;
  background: white;
  border-radius: var(--border-radius-3);
  cursor: pointer;
}

.theme__background-colors button.bg-2 {
  background: #111;
}

/* 모바일 */
@media screen and (max-width: 600px) {
  .card.theme__modal {
    width: var(--container-width-md);
    padding: 3rem 1.5rem;
  }

  .theme__primary-colors {
    gap: 0.6rem;
  }
}
